.card-list{
    width: 85vw;
    height: 200px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
  }

  @media (max-width: 500px){
    .card-list{

      margin-left: -40px;
      width: 0vw;
      height: 190px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      left: 0;
      grid-gap: 10px;
    }
  }