.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.heading{
  font-family: 'Bigelow Rules';
  color: #95dada;
  text-align: center;
  font-size: 72px;
}
.heading-title{
  color: #95dada;
  margin-left: 55%;
  position: relative;
  top: -60px;
  font-size: 20px;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@media (max-width: 500px){
  .heading{
  font-family: 'Bigelow Rules';
  color: #95dada;
  margin-left: 40%;
  font-size: 72px;
}
.heading-title{
  color: #95dada;
  margin-left: 50%;
  position: relative;
  top: -60px;
  font-size: 20px;
}
}